/* Footer.css */
.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(45deg, #E95793, #419197); /* Matching theme colors */
    color: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
}

.footer-content {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}

.footer-links {
    margin-top: 10px;
}

.footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.footer-links a {
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    font-family: "Poppins", sans-serif;
    font-size: 1rem;
}

.footer-links a:hover {
    text-decoration: underline;
}
