/* src/components/Home.css */

.home-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.posts-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}

.post-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s ease-in-out;
}

.post-card:hover {
    transform: scale(1.05);
}

.post-title {
    font-size: 1.5rem;
    margin: 15px;
}

.post-image {
    width: 100%;
    height: auto;
}

.post-body {
    padding: 15px;
    font-size: 1rem;
    color: #333;
}
